import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable'
import 'rxjs/add/operator/map'
import 'rxjs/add/operator/catch'
import 'rxjs/add/observable/throw'

import { Video } from './video';

@Injectable()
export class VideoService {

  private _getUrl = '/api/videos'
  private _postUrl = '/api/video'
  private _putUrl = '/api/video/'
  private _deleteUrl = '/api/video/'

  constructor(
    private _http: Http
  ) { }

  getVideos() {
    return this._http.get(this._getUrl)
      .map((res: Response) => res.json())
      .catch(this._errorHandler)
  }

  addVideo(video: Video) {
    let headers = new Headers({"Content-Type": "application/json"})
    let options = new RequestOptions({headers: headers})
    let body = JSON.stringify(video)
    return this._http.post(this._postUrl, body, options)
      .map((res: Response) => res.json())
      .catch(this._errorHandler)
  }

  updateVideo(video: Video) {
    let putUrl = this._putUrl + video._id
    let headers = new Headers({"Content-Type": "application/json"})
    let options = new RequestOptions({headers: headers})
    let body = JSON.stringify(video)
    return this._http.put(putUrl, body, options)
      .map((res: Response) => res.json())
      .catch(this._errorHandler)
  }

  deleteVideo(video: Video) {
    let deleteUrl = this._deleteUrl + video._id
    return this._http.delete(deleteUrl)
      .map((res: Response) => res.json())
      .catch(this._errorHandler)
  }

  _errorHandler(error: Response) {
    console.error(error)
    return Observable.throw(error)
  }

}



