import { Component, OnInit } from '@angular/core';

import { Video } from './../video';

import { VideoService } from './../video.service';

@Component({
  selector: 'app-video-center',
  templateUrl: './video-center.component.html',
  styleUrls: ['./video-center.component.css']
})
export class VideoCenterComponent implements OnInit {

  videos: Array<Video>

  selectedVideo: Video
  private hidenewVideo: boolean = true

  mensagem: {}
  classesCss: {}
  private currentTimeout: any

  constructor(
    private _videoService: VideoService
  ) { }

  ngOnInit() {
    this._videoService.getVideos()
      .subscribe(
        resVideoData => this.videos = resVideoData,
        resError => {
          console.log(resError)
          this.mostrarMensagem({
            tipo: 'danger',
            mensagem: 'Error occurred for get videos!'
          })
        }
      )
  }

  onSelectVideo(video: any) {
    this.selectedVideo = video
    this.hidenewVideo = true
    console.log(this.selectedVideo)
  }

  onSubmitAddVideo(video: Video) {
    this._videoService.addVideo(video)
      .subscribe(
        resNewVideo => {
          this.videos.push(resNewVideo)
          this.hidenewVideo = true
          this.selectedVideo = resNewVideo
          this.mostrarMensagem({
            tipo: 'success',
            mensagem: 'Video created!!'
          })
        },
        resError => {
          console.log(resError)
          this.mostrarMensagem({
            tipo: 'danger',
            mensagem: 'Error occurred for create!'
          })
        }
      )
  }

  newVideo() {
    this.hidenewVideo = false
  }

  onUpdateVideoEvent(video: Video) {
    this._videoService.updateVideo(video)
      .subscribe(
        resUpdatedVideo => {
          video = resUpdatedVideo
          this.mostrarMensagem({
            tipo: 'success',
            mensagem: 'Video updated!!'
          })
        },
        resError => {
          console.log(resError)
          this.mostrarMensagem({
            tipo: 'danger',
            mensagem: 'Error occurred for update!'
          })
        }
      )
    this.selectedVideo = null
  }

  onDeleteVideoEvent(video: Video) {
    let videoArray = this.videos
    this._videoService.deleteVideo(video)
      .subscribe(
        resDeletedVideo => {
          for (let i=0; i<videoArray.length; i++) {
            if (videoArray[i]._id === video._id) {
              videoArray.splice(i, 1)
            }
          }
          this.mostrarMensagem({
            tipo: 'success',
            mensagem: 'Video deleted!!'
          })
        },
        resError => {
          console.log(resError)
          this.mostrarMensagem({
            tipo: 'danger',
            mensagem: 'Error occurred for delete!'
          })
        }
      )
    this.selectedVideo = null
  }

  private mostrarMensagem(mensagem: {tipo: string, mensagem: string}): void {
    this.mensagem = mensagem
    this.montarClasses(mensagem.tipo)
    if (mensagem.tipo != 'danger') {
      if (this.currentTimeout) {
        clearTimeout(this.currentTimeout)
      }

      this.currentTimeout = setTimeout(() => {
        this.mensagem = undefined
      }, 3000)
    }
  }

  private montarClasses(tipo: string): void {
    this.classesCss = {
      'alert': true
    }
    this.classesCss['alert-'+tipo] = true
  }

}



